body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainBlue: #2a2a72;
  --lightBlue: #009ffd;
  --mainWhite: #f3f3f3;
  --mainDark: #232528;
  --mainYellow: #ffa400;
  --mainRed: #ff0000;
  --mainGrey: #808080;
  --darkGrey: #373b44;
  --babyBlue: #c6e2ff;
  /*  */
  --navy: #141f3a;
  --purp: #42142f;
  --lightGrey: #dbdbdb;
  --drk: #151515;
  --lightBlue: #246079;
}
body {
  background-color: #232528 !important;
  background-color: var(--mainDark) !important;
}

.chart {
  color: #000;
}

.dayLabel {
  font-size: 2em;
  color: #f3f3f3;
  color: var(--mainWhite);
}

.quantityLabel {
  color: #42142f;
  color: var(--purp);
}
.valueLabel {
  color: green;
}
.balanceLabel {
  color: #2a2a72;
  color: var(--mainBlue);
}

.container {
  text-align: left;
  max-width: 100%;
  display: flex;
  justify-content: space-around;
}

.jumbotron {
  margin: 5px;
  background-color: #808080 !important;
  background-color: var(--mainGrey) !important;
  border: 3px solid white;
}

.game-over {
  color: #fff;
  text-align: center;
  margin: 100px;
  justify-content: center;
  border-style: solid;
  border-width: 5px;
}

.buyStockButton {
  width: 150px;
  height: 60px;
  box-shadow: inset 0px 1px 0px 0px #bbdaf7;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0.05, #79bbff),
    color-stop(1, #378de5)
  );
  background: -webkit-linear-gradient(top, #79bbff 5%, #378de5 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #79bbff), to(#378de5));
  background: linear-gradient(to bottom, #79bbff 5%, #378de5 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#79bbff', endColorstr='#378de5',GradientType=0);
  background-color: #79bbff;
  border-radius: 6px;
  border: 1px solid #84bbf3;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #528ecc;
}
.buyStockButton:hover {
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0.05, #378de5),
    color-stop(1, #79bbff)
  );
  background: -webkit-linear-gradient(top, #378de5 5%, #79bbff 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #378de5), to(#79bbff));
  background: linear-gradient(to bottom, #378de5 5%, #79bbff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#378de5', endColorstr='#79bbff',GradientType=0);
  background-color: #378de5;
}
.buyStockButton:active {
  /* position: relative; */
  top: 1px;
}
