:root {
  --mainBlue: #2a2a72;
  --lightBlue: #009ffd;
  --mainWhite: #f3f3f3;
  --mainDark: #232528;
  --mainYellow: #ffa400;
  --mainRed: #ff0000;
  --mainGrey: #808080;
  --darkGrey: #373b44;
  --babyBlue: #c6e2ff;
  --navy: #003366;
  --purp: #452b49;
  --lightGrey: #b4aab6;
}
body {
  background-color: --lightBlue;
  margin: 0px;
  font-family: 'Bitter', serif;
  font-size: 2em;
}
.home {
  background-image: url(/stocks/static/media/bull_md.e88db7e9.jpg);
  background-size: cover;
  background-position: center center;
  position: relative;
  top: 0;
  bottom: 0;
  height: 100vh;
  z-index: 0;
}
.home-overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.75);
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.home-desc {
  text-align: center;
  font-weight: 500;
  color: white;
  text-align: left;
  margin: 1em;
}

.home-objective {
  text-align: center;
  margin: 1em;
  color: #b4aab6;
  color: var(--lightGrey);
  font-weight: 300;
  z-index: 10;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button-choice {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.daily-stats {
  display: flex;
  flex-direction: row;
  color: white;
  position: relative;
  left: 50vh;
}

.title {
  color: white;
  text-align: center;
}

